import { Link } from "react-router-dom";
function Error404(){

    return(
        <>
        <div>
        <h1 ><i class="fas fa-exclamation-triangle"></i>
        Página no existente<i class="fas fa-exclamation-triangle"></i></h1>
        <strong>Confirma que la ruta sea la correcta</strong>
        <a className="bottom"><Link to="/">Volver</Link></a>
        
        </div>
    </>
    );
}

export default Error404;