import './Header.css';
import foto from './foto1.jpg';


function Header(){
    return(
        <header>
        <div className="col">
            <div className="card">
                <div className="card-header"> 
                    <h1>Hoja de vida</h1>
                </div >
                <div className="row">
                    <div className="col-md-4">
                        <img className="header-foto" src={foto} alt="" />
                    </div>
                    <div className="col-md-8">
                        <h2 className="header-name">Daniel Felipe Parra Suarez </h2>
                        <h3> <span>Analizador de sistemas de información</span></h3>
                    </div>
                </div>
            </div>
        </div>    
        </header>

    );
}

export default Header;